.wp-block-search {
    .wp-block-search__label {
        font-size: 15px;
        font-weight: 400;
        margin-bottom: .25rem;
        display: block;
    }
    & .wp-block-search__input,
    &.wp-block-search__button-inside .wp-block-search__inside-wrapper .wp-block-search__input {
        @include th-input;

        margin: 0;
    }
    .wp-block-search__button {
        @include th-button-base;
        @include th-button--size--normal;
        @include th-button--style--primary;

        box-shadow: none;
        font-family: inherit;

        @include direction {
            #{$margin-inline-start}: 10px;
        }

        svg {
            margin: -4px;
            display: block;
        }
    }

    &.wp-block-search__button-inside {
        .wp-block-search__inside-wrapper {
            border: none;
            padding: 0;

            .wp-block-search__button {
                padding: 8.5px 16px;

                @include direction {
                    #{$margin-inline-start}: 0;
                }
            }
        }
    }
}
