@mixin th-decor-line($line-length) {
    position: relative;

    @include direction {
        #{$padding-inline-start}: $line-length + 6px;
    }

    &:before {
        position: absolute;
        display: block;
        content: '';
        height: 1px;
        width: $line-length;
        background: currentColor;
        opacity: .6;
        top: 10px;

        @include direction {
            #{$inset-inline-start}: 0;
        }
    }
}
