@mixin local-layout($gutter) {
    li {
        padding-top: $gutter;
        padding-bottom: $gutter;
    }
    & > li {
        &:before {
            top: ((20px + $gutter * 2 - 4px) / 2);
        }
    }
    li > ul {
        margin-top: $gutter;
        margin-bottom: -$gutter;
    }
}

ul.wp-block-archives-list,
.wp-block-categories ul {
    padding: 0;
}

.wp-block-archives-list,
.wp-block-categories-list,
.wp-block-categories > .wp-block-categories__list {
    &,
    ul {
        padding: 0;
        list-style: none;
        line-height: 20px;
        color: map-get($card-scheme, muted);
    }
    ul {
        margin: 0;
    }

    li {
        @include th-reset-list-item;

        position: relative;
        font-size: 12px;
        line-height: 20px;

        @include direction {
            #{$padding-inline-start}: 14px;
        }
    }
    & > li {
        &:before {
            position: absolute;
            display: block;
            content: '';
            width: 4px;
            height: 4px;
            background: currentColor;
            opacity: .2;

            @include direction {
                #{$inset-inline-start}: 0;
            }
        }
    }
    a {
        font-size: 15px;
        color: map-get($card-scheme, opposite);
        transition: color .12s;
        vertical-align: bottom;
        text-decoration: none;

        &:hover {
            @include th-var(color, --th-link-color, $link-color);
        }
    }

    @include local-layout($gutter: 5px);
}

@if ($th-is-editor) {
    .wp-block-categories select,
    .wp-block-archives-dropdown select {
        @include th-select;

        margin: 0;
    }
}
