@mixin th-local-blockquote() {
    blockquote,
    .wp-block-quote {
        @content;
    }
}
@mixin th-local-blockquote__cite() {
    @include th-local-blockquote {
        cite {
            @content;
        }
    }
}
@mixin th-local-blockquote__cite--after-paragraph() {
    @include th-local-blockquote {
        p + cite {
            @content;
        }
    }
}

@include th-local-blockquote {
    background: #f7f7f7;
    padding: 16px 20px;

    @include direction {
        #{$border-inline-start}: 3px solid #dedede;
    }
}
@include th-local-blockquote__cite {
    display: block;
    font-size: 14px;
    color: #6c757d;
}
@include th-local-blockquote__cite--after-paragraph {
    margin-top: -16px;
}

.wp-block-quote {
    &.has-text-align-right { // SKIP-RTL
        padding: 16px 20px;

        @include direction {
            #{$border-inline-start}: none;
            #{$border-inline-end}: 3px solid #dedede;
        }
    }
}
.wp-block-quote.is-style-large {
    padding: 28px 32px;

    @include direction {
        #{$border-inline-start}: 3px solid #dedede;
    }

    cite {
        @include direction {
            text-align: $inline-start;
        }
    }

    p + cite {
        margin-top: -12px;
    }
}
.wp-block-pullquote {
    padding: 32px;
    color: inherit;

    &:not(.has-background) {
        border-top: 3px solid #dedede;
        border-bottom: 3px solid #dedede;
    }

    blockquote {
        padding: 0;
        border: none;
        background: transparent;
    }

    cite {
        display: block;
        font-size: 14px;
        color: #6c757d;
    }

    p + cite {
        margin-top: 12px;
    }

    blockquote,
    blockquote p {
        margin: 0 auto;
    }
}

@if ($th-is-editor) {
    .wp-block-quote__citation {
        font-size: 14px;
        color: #6c757d;
        margin-top: 8px;
        font-style: italic;

        .is-style-large & {
            font-size: 1.125em;
            margin-top: 12px;
        }
    }
    .wp-block-pullquote__citation {
        font-size: 14px;
        color: #6c757d;
        margin-top: 12px;
        font-style: italic;
        text-transform: none;
    }
}
