.wp-block-rss {
    &,
    [data-block] & {
        padding: 0;
        list-style: none;
    }
}
.wp-block-rss__item {
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
}
.wp-block-rss__item + .wp-block-rss__item {
    margin-top: 12px;
    padding-top: 12px;
    border-top: 1px solid #ebebeb;
}
.wp-block-rss__item-title {
    width: 100%;
    font-size: 15px;
    font-weight: 500;

    a {
        color: inherit;
        transition: color .12s;
        text-decoration: none;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }
}
.wp-block-rss__item-excerpt {
    font-size: 0.875em;
    line-height: inherit;
    margin-top: 6px;
    color: #6c757d;
}
.wp-block-rss__item-publish-date,
.wp-block-rss__item-author {
    font-size: 13px;
    color: map_get($card-scheme, muted);
    margin-top: 1px;

    @include direction {
        #{$padding-inline-start}: 6px;
    }
}
.wp-block-rss__item-title + .wp-block-rss__item-publish-date,
.wp-block-rss__item-title + .wp-block-rss__item-author {
    @include th-decor-line(16px);
}
