.wp-block-latest-posts {
    & > li > a {
        font-size: 15px;
        font-weight: 500;
        color: inherit;
        transition: color .12s;
        text-decoration: none;

        &:hover {
            @include th-var(color, --th-link-color, $link-color);
        }
    }

    @for $i from 1 through 6 {
        &.columns-#{$i} {
            & > li {
                margin-top: 0;
                margin-bottom: 0;
            }
            & > li:nth-child(n+#{$i + 1}) {
                margin-top: 20px;
            }
        }
    }

    @media (max-width: 599px) {
        @for $i from 1 through 6 {
            &.columns-#{$i} {
                & > li:nth-child(n+2) {
                    margin-top: 20px;
                }
            }
        }
    }
}
.wp-block-latest-posts__post-author,
.wp-block-latest-posts__post-date {
    font-size: 13px;
    color: map_get($card-scheme, muted);
    margin-top: 1px;
}
.wp-block-latest-posts:not(.is-grid) {
    padding: 0;

    & > li + li {
        margin: 14px 0 0;
    }
}
