body {
    background: map_get($body-scheme, main);
    color: map_get($body-scheme, opposite);
    font-family: $body-font-family;
    font-size: $body-font-size;
    font-weight: $body-font-weight;
    line-height: $body-line-height;
    backface-visibility: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.25;
    font-weight: 500;
}

h1 {font-size: 2.5rem;}
h2 {font-size: 2rem;}
h3 {font-size: 1.75rem;}
h4 {font-size: 1.5rem;}
h5 {font-size: 1.25rem;}
h6 {font-size: 1rem;}

a {
    @include th-var(color, --th-link-color, $link-color);
}
a:hover {
    @include th-var(color, --th-link-color, $link-color);
    text-decoration: none;
}

p {
    line-height: 1.625;
}

ol,
ul {
    margin-left: 0; // SKIP-RTL
    margin-right: 0; // SKIP-RTL

    @include direction {
        #{$padding-inline-start}: 24px;
    }

    li {
        margin: 4px 0;
    }
}

table {
    width: 100%;

    th,
    td {
        border: 1px solid #e5e5e5;
        padding: .375rem .625rem;
    }
}

dd {
    margin-bottom: .75rem;

    @include direction {
        #{$margin-inline-start}: 1.25rem;
    }
}

pre {
    background: #f7f7f7;
    padding: 16px 20px;
    font-size: .875em;
    line-height: 1.375;
    border-radius: 4px;
    border: none;
}

hr {
    margin-top: 32px;
    margin-bottom: 32px;
    background: #e5e5e5;
    border: none;
    height: 2px;
    width: 100%;
    opacity: 1;
    overflow: visible;
}
