.wp-block-button__link {
    border-radius: 2px;
    font-size: 15px;
    font-weight: 500;
    line-height: 19px;
    padding: 8.5px 16px;
    user-select: none;
    transition:
        color .15s ease-in-out,
        background-color .15s ease-in-out,
        border-color .15s ease-in-out,
        box-shadow .15s ease-in-out;

    &:not(.has-text-color) {
        @include th-var(color, --th-btn-primary-normal-font-color, #fff);
    }
    &:not(.has-background) {
        @include th-var(background, --th-btn-primary-normal-bg-color, #e52727);
    }

    .is-style-outline > & {
        padding: (8.5px - 2px) (16px - 2px);

        &:not(.has-text-color) {
            @include th-var(color, --th-btn-primary-normal-bg-color, #e52727);
            @include th-var(border-color, --th-btn-primary-normal-bg-color, #e52727);
        }
        &:not(.has-background) {
            background: transparent;
        }
    }

    .wp-block-button &,
    .is-style-outline > & {
        &:hover,
        &:focus {
            @include th-var(color, --th-btn-primary-hover-font-color, #fff);
            @include th-var(border-color, --th-btn-primary-hover-bg-color, #333);
            @include th-var(background, --th-btn-primary-hover-bg-color, #333);
        }
        &:active {
            @include th-var(color, --th-btn-primary-active-font-color, #fff);
            @include th-var(border-color, --th-btn-primary-active-bg-color, #4d4d4d);
            @include th-var(background, --th-btn-primary-active-bg-color, #4d4d4d);
        }
    }
}
