.wp-block-tag-cloud:not(.wp-block) {
    $local-gutter: 4px;

    display: flex;
    flex-wrap: wrap;
    font-weight: $font-weight-medium;
    margin-top: -$local-gutter;

    @include direction {
        #{$margin-inline-end}: -$local-gutter;
    }

    .tag-cloud-link {
        display: block;
        margin-top: $local-gutter;
        background: $tag-normal-bg-color;
        padding: 2px 8px;
        color: inherit;
        border-radius: 1.5px;
        transition: background .12s;
        font-size: 13px !important;
        text-decoration: none;

        @include direction {
            #{$margin-inline-end}: $local-gutter;
        }

        &:hover {
            color: inherit;
            background: $tag-hover-bg-color;
        }
        &:active {
            transition-duration: 0s;
            background: $tag-active-bg-color;
        }
    }
}
