.wp-block-gallery {
    &.alignleft,
    &.alignright,
    [data-align="left"] > &,
    [data-align="right"] > & {
        max-width: 320px;
    }

    @for $i from 1 through 9 {
        &.columns-#{$i} {
            .blocks-gallery-item {
                margin-top: 0;
                margin-bottom: 0;
            }
            .blocks-gallery-item:nth-child(n+#{$i + 1}) {
                margin-top: 16px;
            }
        }
    }

    @include media-breakpoint-down(sm) {
        @for $i from 1 through 9 {
            &.columns-#{$i} {
                .blocks-gallery-item {
                    width: 100%;
                    margin-left: 0;
                    margin-right: 0;
                }
                .blocks-gallery-item:nth-child(n+1) {
                    margin-top: 16px;
                }
            }
        }
    }
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;

    &:last-child {
        margin-bottom: 0;
    }
}
.gallery-item {
    img {
        margin-left: auto;
        margin-right: auto;
    }
}
.gallery-icon {
    a,
    img {
        display: block;
    }
}

@for $i from 1 through 9 {
    .gallery-columns-#{$i} {
        --th-gallery-columns: #{$i};

        .gallery-item {
            margin: 0;
            width: calc((100% - 16px * #{$i - 1}) / #{$i});
        }
        .gallery-item:nth-child(n) {
            margin-left: 16px; // SKIP-RTL
        }
        .gallery-item:nth-child(#{$i}n + 1) {
            margin-left: 0; // SKIP-RTL
        }
        .gallery-item:nth-child(n+#{$i + 1}) {
            margin-top: 16px;
        }
    }
}

@include media-breakpoint-down(md) {
    @for $i from 1 through 9 {
        .gallery-columns-#{$i} {
            .gallery-item {
                margin: 0;
                width: calc((100% - 16px) / 2);
            }
            .gallery-item:nth-child(n) {
                margin-left: 16px; // SKIP-RTL
            }
            .gallery-item:nth-child(2n-1) {
                margin-left: 0; // SKIP-RTL
            }
            .gallery-item:nth-child(n+3) {
                margin-top: 16px;
            }
        }
    }
}
@include media-breakpoint-down(sm) {
    @for $i from 1 through 9 {
        .gallery-columns-#{$i} {
            .gallery-item {
                margin: 0;
                width: 100%;
            }
            .gallery-item:nth-child(n) {
                margin-left: 0; // SKIP-RTL
            }
            .gallery-item:nth-child(n+1) {
                margin-top: 16px;
            }
        }
    }
}
