.wp-block-latest-comments {
    --th-avatar-size: 38px;
    --th-avatar-gutter: 14px;

    padding: 0;

    .avatar {
        width: var(--th-avatar-size);
        height: var(--th-avatar-size);
        border-radius: calc(var(--th-avatar-size) / 2);

        @include direction {
            #{$margin-inline-start}: 0;
            #{$margin-inline-end}: var(--th-avatar-gutter);
            float: $inline-start;
        }
    }

    &:not(.has-dates) {
        --th-avatar-size: 20px;
        --th-avatar-gutter: 10px;
    }
}
.wp-block-latest-comments__comment {
    margin: 0;

    & + .wp-block-latest-comments__comment {
        margin-top: 8px;
    }

    .has-avatars & {
        & + .wp-block-latest-comments__comment {
            margin-top: 12px;
        }
    }

    .has-dates &,
    .has-excerpts & {
        & + .wp-block-latest-comments__comment {
            margin-top: 16px;
        }
    }

    &:after {
        display: block;
        content: '';
        clear: both;
    }
}
.has-avatars .wp-block-latest-comments__comment {
    min-height: 0;
}
.has-avatars .wp-block-latest-comments__comment .wp-block-latest-comments__comment-meta {
    @include direction {
        #{$margin-inline-start}: calc(var(--th-avatar-size) + var(--th-avatar-gutter));
    }

    &:after {
        display: block;
        content: '';
        clear: both;
    }
}
.wp-block-latest-comments__comment-excerpt,
.has-avatars .wp-block-latest-comments__comment .wp-block-latest-comments__comment-excerpt {
    margin: 12px 0 0;
    width: 100%;
    background: #f5f5f5;
    padding: 12px 17px;
    border-radius: 5px;
    font-size: 15px;
    position: relative;

    &:before {
        $local-arrow-size: 10px;

        position: absolute;
        content: '';
        display: block;
        top: -$local-arrow-size;
        border: ($local-arrow-size / 2) solid transparent;
        border-bottom-color: map_get($child-card-scheme, main);
        pointer-events: none;

        @include direction {
            #{$inset-inline-start}: calc(var(--th-avatar-size) / 2 - #{$local-arrow-size / 2});
        }
    }
}
.wp-block-latest-comments__comment-meta {
    font-size: 15px;
    line-height: 1.375;
    color: #262626;
}
.wp-block-latest-comments__comment-date {
    font-size: 13px;
    margin-top: 1px;
    color: map_get($card-scheme, muted);
}
.wp-block-latest-comments__comment-author {
    font-weight: 500;
    color: #262626;
    text-decoration: none;
}
.wp-block-latest-comments__comment-link {
    color: #262626;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}
.wp-block-latest-comments__comment-excerpt p {
    font-size: 0.9375em;
    line-height: inherit;
}
