@mixin th-select {
    font-weight: $font-weight-normal;
    display: block;
    width: 100%;
    border-radius: 2px;
    background-clip: border-box;
    appearance: none;
    color: #262626;
    background-color: #ebebeb;
    border: 1px solid #ebebeb;
    height: 36px;
    font-size: 16px;
    font-family: inherit;
    line-height: 19px;
    padding: 7.5px 10px;
    background-image: url(svg-select-arrow($form-control-select-arrow-color));
    background-repeat: no-repeat;
    background-size: 5px 10px;
    cursor: pointer;
    max-width: 100%;
    text-shadow: none;
    transition:
        border-color .15s ease-in-out,
        box-shadow .15s ease-in-out,
        background .15s ease-in-out;

    @include direction {
        #{$padding-inline-end}: 27px;
        background-position: $inline-end 10px center;
    }

    &:hover {
        border-color: #d9d9d9;
    }
    &:focus {
        border-color: #d9d9d9;
        background-color: #fff;
    }

    &:disabled {
        background-color: #ebebeb;
        border-color: #ebebeb;
        color: #262626;
        cursor: default;
        opacity: .65;
    }
}
@mixin th-select--size--small {
    height: 30px;
    padding: 5.5px 8px;
    font-size: 14px;
    line-height: 17px;

    @include direction {
        #{$padding-inline-end}: 23px;
        background-position: $inline-end 8px center;
    }
}
