@mixin th-input-elements {
    textarea,
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="tel"],
    input[type="number"] {
        @content;
    }
}
@mixin th-input-single-line-elements {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="tel"] {
        @content;
    }
}
@mixin th-input {
    border-radius: 2px;
    background-clip: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out,background .15s ease-in-out;
    color: #262626;
    background-color: #ebebeb;
    border: 1px solid #ebebeb;
    padding: 7.5px 10px;
    font-size: 16px;
    line-height: 19px;
    min-height: (1px * 2 + 7.5px * 2 + 19px);
    vertical-align: middle;
    max-width: 100%;
    width: 100%;
    font-family: inherit;

    &:hover {
        border-color: #d9d9d9;
    }
    &:focus {
        outline: none;
        border-color: #d9d9d9;
        background-color: #fff;
    }
}
@mixin th-input--single-line {
    height: (1px * 2 + 7.5px * 2 + 19px);
}


@mixin input-sm() {
    min-height: 30px;
    height: 30px;
    padding: 5.5px 8px;
    font-size: 14px;
    line-height: 17px;
}
@mixin input-nl() {
    height: 36px;
    padding: 7.5px 10px;
    font-size: 16px;
    line-height: 19px;
}
@mixin input-lg() {
    height: 42px;
    padding: 9.5px 12px;
    font-size: 18px;
    line-height: 21px;
}
