@mixin th-button-base {
    position: relative;
    text-align: center;
    vertical-align: middle;
    display: inline-block;
    border: none;
    border-radius: 2px;
    fill: currentColor;
    font-weight: 500;
    cursor: pointer;
    transition:
        color .15s ease-in-out,
        background-color .15s ease-in-out,
        border-color .15s ease-in-out,
        box-shadow .15s ease-in-out;

    &:focus {
        outline: none;
    }
    &:disabled {
        cursor: default;
        opacity: .65;
    }
}
@mixin th-button--style--primary {
    @include th-var(background, --th-btn-primary-normal-bg-color, #e52727);
    @include th-var(color, --th-btn-primary-normal-font-color, #fff);

    &:hover,
    &:focus {
        @include th-var(background, --th-btn-primary-hover-bg-color, #e52727);
        @include th-var(color, --th-btn-primary-hover-font-color, #fff);
    }
    &:active {
        @include th-var(background, --th-btn-primary-active-bg-color, #e52727);
        @include th-var(color, --th-btn-primary-active-font-color, #fff);
    }
    &:disabled {
        @include th-var(background, --th-btn-primary-normal-bg-color, #e52727);
        @include th-var(color, --th-btn-primary-normal-font-color, #fff);
    }
}
@mixin th-button--style--secondary {
    @include th-var(background, --th-btn-secondary-normal-bg-color, #f0f0f0);
    @include th-var(color, --th-btn-secondary-normal-font-color, #333);

    &:hover,
    &:focus {
        @include th-var(background, --th-btn-secondary-hover-bg-color, #e5e5e5);
        @include th-var(color, --th-btn-secondary-hover-font-color, #333);
    }
    &:active {
        @include th-var(background, --th-btn-secondary-active-bg-color, #d6d6d6);
        @include th-var(color, --th-btn-secondary-active-font-color, #333);
    }
    &:disabled {
        @include th-var(background, --th-btn-secondary-normal-bg-color, #f0f0f0);
        @include th-var(color, --th-btn-secondary-normal-font-color, #333);
    }
}
@mixin th-button--style--muted {
    @include th-var(background, --th-btn-muted-normal-bg-color, #fff);
    @include th-var(color, --th-btn-muted-normal-font-color, #ccc);

    &:hover,
    &:focus {
        @include th-var(background, --th-btn-muted-hover-bg-color, #f2f2f2);
        @include th-var(color, --th-btn-muted-hover-font-color, #4d4d4d);
    }
    &:active {
        @include th-var(background, --th-btn-muted-active-bg-color, #ebebeb);
        @include th-var(color, --th-btn-muted-active-font-color, #4d4d4d);
    }
    &:disabled {
        @include th-var(background, --th-btn-muted-normal-bg-color, #fff);
        @include th-var(color, --th-btn-muted-normal-font-color, #ccc);
    }
}
@mixin th-button--style--ghost-dark {
    background: rgba(#000, .1);
    color: inherit;

    &:hover,
    &:focus {
        background: rgba(#000, .15);
        color: inherit;
    }
    &:active {
        background: rgba(#000, .2);
        color: inherit;
    }
    &:disabled {
        background: rgba(#000, .1);
        color: inherit;
    }
}
@mixin th-button--size--extra-small {
    font-size: 13px;
    line-height: 15px;
    padding: 4.5px 9px;
}
@mixin th-button--size--small {
    font-size: 14px;
    line-height: 17px;
    padding: 6.5px 12px;
}
@mixin th-button--size--normal {
    font-size: 15px;
    line-height: 19px;
    padding: 8.5px 16px;
}
@mixin th-button--size--large {
    font-size: 18px;
    line-height: 22px;
    padding: 10px 24px;
}
@mixin th-button--size--extra-large {
    font-size: 20px;
    line-height: 26px;
    padding: 17px 40px;
}
