.wp-block-separator {
    margin-top: 32px;
    margin-bottom: 32px;
    background: #e5e5e5;
    border: none;
    height: 2px;
    width: 64px;
    opacity: 1;
    overflow: visible;

    &.is-style-wide {
        width: 100%;
    }
    &.is-style-dots {
        width: 3px;
        height: 3px;
        position: relative;
        background: currentColor !important;

        &:not(.has-text-color) {
            color: #d9d9d9;
        }

        &:before,
        &:after {
            display: block;
            content: '';
            position: absolute;
            width: 3px;
            height: 3px;
            padding: 0;
            background: inherit;
        }
        &:before {
            left: -32px; // SKIP-RTL
            right: auto; // SKIP-RTL
        }
        &:after {
            left: auto; // SKIP-RTL
            right: -32px; // SKIP-RTL
        }
    }
}
