.wp-block-cover-image,
.wp-block-cover {
    min-width: 100%;
    width: auto;
    padding: 2.5em;

    &.alignleft,
    &.alignright,
    [data-align="left"] > &,
    [data-align="right"] > & {
        min-width: auto;
        max-width: 320px;
    }
}
.wp-block-cover__inner-container {
    max-width: calc(#{(760px - 48px * 2)} - 5em);
}
