$th-is-editor: false !default;

h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
dl,
blockquote,
address,
table,
.wp-caption,
.wp-block-image,
.wp-block-button,
.wp-block-cover,
.wp-block-gallery,
.wp-block-columns,
.wp-block-quote,
.wp-block-quote.is-style-large,
.wp-block-pullquote,
.wp-block-file,
.wp-block-video,
.wp-block-embed,
.wp-block-group,
.wp-block-media-text,
.wp-block-code,
.wp-block-preformatted,
.wp-block-table,
.wp-block-verse,
.wp-block-archives-list,
.wp-block-archives-dropdown,
.wp-block-categories-list,
.wp-block-categories-dropdown,
.wp-block-latest-comments,
.wp-block-latest-posts,
.wp-block-search,
.wp-block-tag-cloud,
.wp-block-rss {
    margin-top: 24px;
    margin-bottom: 24px;

    &:first-child {
        margin-top: 0;
    }
    &:last-child {
        margin-bottom: 0;
    }
}
.wp-block-column {
    & > :first-child {
        margin-top: 0;
    }
    & > :last-child {
        margin-bottom: 0;
    }
}

@import 'editor/typography';
@import 'editor/block-latest-comments';
@import 'editor/block-search';
@import 'editor/block-tag-cloud';
@import 'editor/block-categories';
@import 'editor/block-latest-posts';
@import 'editor/block-rss';
@import 'editor/block-quote';
@import 'editor/block-cover';
@import 'editor/block-button';
@import 'editor/block-separator';
@import 'editor/block-media-text';
@import 'editor/block-file';
@import 'editor/block-gallery';
@import 'editor/block-embed-twitter';
@import 'wordpress/widgets/widget_calendar';

// Align.
.wp-block-image,
.wp-block-button,
.wp-block-cover,
.wp-block-gallery,
.wp-block-video,
.wp-block-embed {
    &.alignleft, // SKIP-RTL
    & .alignleft, // SKIP-RTL
    &.alignright, // SKIP-RTL
    & .alignright { // SKIP-RTL
        margin-top: 0;
        margin-bottom: 20px;
    }
    &:last-child {
        .alignleft, // SKIP-RTL
        .alignright { // SKIP-RTL
            margin-bottom: 0;
        }
    }
    &.alignleft, // SKIP-RTL
    & .alignleft { // SKIP-RTL
        @include direction {
            float: $inline-start;

            #{$margin-inline-start}: 0;
            #{$margin-inline-end}: 20px;
        }
    }
    &.alignright, // SKIP-RTL
    & .alignright { // SKIP-RTL
        @include direction {
            float: $inline-end;

            #{$margin-inline-start}: 20px;
            #{$margin-inline-end}: 0;
        }
    }

    &:first-child + * {
        margin-top: 0;
    }
}
.alignwide,
.alignfull {
    &,
    &.wp-block-gallery {
        margin-left: -48px; // SKIP-RTL
        margin-right: -48px; // SKIP-RTL

        @include media-breakpoint-down(sm) {
            margin-left: -32px; // SKIP-RTL
            margin-right: -32px; // SKIP-RTL
        }
        @include media-breakpoint-down(xs) {
            margin-left: -24px; // SKIP-RTL
            margin-right: -24px; // SKIP-RTL
        }
    }
}
.alignwide,
.alignfull {
    &.wp-block-columns {
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
    }
}

// Caption.
%th-caption {
    text-align: center;
    margin-top: 14px;
    margin-bottom: 0;
    font-size: 14px;
    color: #6c757d;
}

.wp-block-image,
.wp-block-gallery,
.wp-block-video,
.wp-block-embed,
.wp-block-table {
    & > figure > figcaption,
    & > figcaption {
        @extend %th-caption;
    }
}
.wp-caption-text,
.gallery-caption {
    @extend %th-caption;
}
.wp-caption {
    max-width: 100%;
}




.has-drop-cap:not(:focus)::first-letter {
    font-size: 8.4em;
    line-height: 0.68;
    font-weight: 400;
    margin: 0.05em 0.1em 0 0;
    text-transform: uppercase;
    font-style: normal;

    @include direction {
        float: $inline-start;
    }
}

.wp-block-group.has-background {
    padding: 24px 32px;
}






.wp-block-video {
    video {
        vertical-align: middle;
    }
}
.wp-block-image {
    figure {
        margin: 0;
    }
    img {
        display: block;
    }

    &.alignwide,
    &.alignfull {
        img {
            height: auto;
        }
    }
}


.has-large-font-size {
    &,
    .editor-styles-wrapper & {
        line-height: 1.375;
    }
}


@media (max-width: 781px) and (min-width: 600px) {
    .wp-block-column:nth-child(n + 3) {
        margin-top: 24px;
    }
    .wp-block-column:nth-child(even) {
        @include direction {
            #{$margin-inline-start}: 24px;
            #{$margin-inline-end}: 0;
        }
    }
}
@media (max-width: 599px) {
    .wp-block-column:nth-child(n + 2) {
        margin-top: 24px;
    }
}



// editor only
.wp-block {
    max-width: 664px;

    &[data-align="wide"] .wp-block-gallery {
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
    }

    &[data-block].wp-block-gallery.alignfull,
    &[data-block].wp-block-gallery.alignwide {
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
        max-width: 100%;
    }
    &[data-block].wp-block-gallery.alignleft {
        @include direction {
            #{$margin-inline-start}: 48px;

            @include media-breakpoint-down(sm) {
                #{$margin-inline-start}: -32px; // SKIP-RTL
            }
            @include media-breakpoint-down(xs) {
                #{$margin-inline-start}: -24px; // SKIP-RTL
            }
        }
    }
    &[data-block].wp-block-gallery.alignright {
        @include direction {
            #{$margin-inline-end}: 48px;

            @include media-breakpoint-down(sm) {
                #{$margin-inline-end}: -32px; // SKIP-RTL
            }
            @include media-breakpoint-down(xs) {
                #{$margin-inline-end}: -24px; // SKIP-RTL
            }
        }
    }
}
.wp-block > .wp-block-cover,
.wp-block > .wp-block-media-text,
.is-root-container > .block-editor-block-list__block {
    &,
    &:first-child {
        margin-top: 24px;
    }
    &,
    &:last-child {
        margin-bottom: 24px;
    }
}
.wp-block[data-align="left"],
.wp-block[data-align="right"] {
    & > .wp-block-cover,
    & > .wp-block-media-text {
        &,
        &:first-child {
            margin-top: 0;
        }
    }
}
.is-root-container > .wp-block-separator {
    margin-top: 32px;
    margin-bottom: 32px;
}
.block-library-spacer__resize-container {
    margin-bottom: 24px;
}
.editor-post-title .editor-post-title__input {
    padding: 0;
    font-size: 36px;
    font-weight: 700;
}
.edit-post-visual-editor__post-title-wrapper .editor-post-title {
    margin-top: 0;
    margin-bottom: 24px;
    padding-top: 36px;
}
.block-editor__typewriter {
    padding: 14px 0;
}
.block-editor-writing-flow {
    max-width: 760px;
    margin-left: auto; // SKIP-RTL
    margin-right: auto; // SKIP-RTL
    background-color: #fff;
    box-shadow: 0 1px 3px rgba(#000, .09);

    &:after {
        content: '';
        display: block;
        height: 1px;
        margin-top: (36px - 1px);
    }
}
figure.wp-block-image:not(.wp-block) {
    margin-top: 24px;
    margin-bottom: 24px;
}
.wp-block-image .components-resizable-box__container {
    vertical-align: middle;
}

.wp-block[data-type="core/cover"] {
    width: 100%;
    min-width: auto;
}

.wp-block[data-align="left"] > *, // SKIP-RTL
.wp-block[data-align="right"] > * { // SKIP-RTL
    margin-top: 0;

    &,
    &:last-child {
        margin-bottom: 20px;
    }
}
.wp-block[data-align="left"] > * { // SKIP-RTL
    @include direction {
        #{$margin-inline-end}: 20px;
    }
}
.wp-block[data-align="right"] > * { // SKIP-RTL
    @include direction {
        #{$margin-inline-start}: 20px;
    }
}

div[data-type="core/button"] {
    display: block;
}

.wp-block[data-align="left"] > .wp-block-image, // SKIP-RTL
.wp-block[data-align="right"] > .wp-block-image { // SKIP-RTL
    margin-top: 0;
    margin-bottom: 20px;
}
.wp-block[data-align="left"] > .wp-block-image { // SKIP-RTL
    @include direction {
        #{$margin-inline-end}: 20px;
    }
}
.wp-block[data-align="right"] > .wp-block-image { // SKIP-RTL
    @include direction {
        #{$margin-inline-start}: 20px;
    }
}
.wp-block[data-align="wide"],
.wp-block[data-align="full"] {
    max-width: 760px;

    & > .wp-block-columns {
        max-width: 664px;
        margin-left: auto; // SKIP-RTL
        margin-right: auto; // SKIP-RTL
    }
    & > .wp-block-media-text,
    &.wp-block-gallery {
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
    }
}
.block-editor-block-list__layout.is-root-container > .wp-block[data-align="full"] {
    margin-left: 0; // SKIP-RTL
    margin-right: 0; // SKIP-RTL
}




// not editor
.aligncenter {
    margin: 0 auto;
    display: block;
    width: fit-content;
}
.alignleft, // SKIP-RTL
.alignright { // SKIP-RTL
    margin-top: 0;
    margin-bottom: 20px;
}
.alignleft { // SKIP-RTL
    @include direction {
        float: $inset-inline-start;

        #{$margin-inline-end}: 20px;
    }
}
.alignright { // SKIP-RTL
    @include direction {
        float: $inset-inline-end;

        #{$margin-inline-start}: 20px;
    }
}
